<template>
<div>
    <ul class="nav nav-tabs">
        <li v-for="(item) in getproductitem" 
            :key="item.id" 
            :class="{'active':item.code===getfilter}">
                <a href="#" @click.prevent="getfilterStr(item)">
                    {{item.name}}
                </a>
        </li>
    </ul>
</div>
</template>
<script>
export default {
    name:'Items',
    //
    // :getordercard='getProducts'
    //getordercard
    props:['getproductitem','getfilter'],
    data(){
        return {

        }
    },
    methods:{
        getfilterStr(item){
            //
            //postPid  
            //console.log('in item  :',item);          
            this.$emit('filterChange',item);
        },
    },
    computed:{

    }
}
</script>


