<template>
<div>
    <div class="tab-content">
        <div class="row mt-4">
            <div class="col-md-4 mb-4" v-for="(item) in getordercard" :key="item.id">
                <div class="card border-0 shadow-sm">
                    <div style="height: 150px; background-size: cover; background-position: center"
                    :style="{backgroundImage:`url(${item.imageUrl})`}"
                    >
                    </div>
                    <div class="card-body">
                    <span class="badge badge-secondary float-right ml-2">{{ item.category }}</span>
                    <h5 class="card-title">
                        <a href="#" class="text-dark">{{ item.title }}</a>
                    </h5>
                    <p class="card-text">{{ item.description }}</p>
                    <div class="d-flex justify-content-between align-items-baseline">
                        <!-- <div class="h5">2,800 </div> -->
                        <del class="h6"> {{ item.origin_price | currency }} </del>
                        <div class="h5"> {{ item.price | currency }} </div>
                    </div>
                    </div>
                    <div class="card-footer d-flex">
                    <button type="button" 
                            class="btn btn-outline-secondary btn-sm"
                            @click="getProduct(item.id)">
                        <i class="fas fa-arrow-down"></i>
                        
                    </button>
                    <button type="button" class="btn btn-outline-danger btn-sm ml-auto"
                            @click="addtoCart(item.id)">
                        <i class="fas fa-shopping-cart"></i>
                        
                    </button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</div>    
</template>
<script>
export default {
    name:'OrderCard',
    //
    // :getordercard='getProducts'
    //getordercard
    props:['getordercard'],
    data(){
        return {
            //
            buynum:1,
            //
            products:[],
            //
            product:{},
            //
            cart: {},
            //
            isLoading:false,
            status:{},
            coupon_code:'',
        }
    },
    methods:{
        getProduct(pid){
            //
            //postPid
            this.$emit('postPid',pid);
        },
        addtoCart(pid){
            //
            //postCartPid
            this.$emit('postCartPid',pid);
        }
    },
    computed:{

    }
}
</script>


