<template>
    <div class="container">
         <div class="row mt-4 productpadding">
            <a href="#" class="cardpadding" v-for="(item) in getordercard" :key="item.id">
                <router-link :to="'/product/'+item.id" >                 
                    <figure                     
                        :style="{backgroundImage:`url(${item.imageUrl})`}"
                        >
                        <div class="date">
                            <span class="card-date-day"> {{ item.price | currency }} </span>
                            <span class="card-date-month"> {{ item.origin_price | currency }} </span></div>
                        <figcaption>
                        <h4>
                            <span>{{ item.title }}</span>
                        </h4>                   
                        </figcaption>
                    </figure>
                </router-link>
            </a>
        </div>
    </div>
</template>
<script>
export default {
    name:'OrderCard2',    
    //
    // :getordercard='getProducts'
    //getordercard
    props:['getordercard','getproductitem'],
    data(){
        return {
            //
            buynum:1,
            //
            products:[],
            //
            product:{},
            //
            cart: {},
            //
            isLoading:false,
            status:{},
            coupon_code:'',
        }
    },
    methods:{
        addtoCart(pid){
            //
            //postCartPid
            this.$emit('postCartPid',pid);
        }
    },
    computed:{

    }
}
</script>


